.gameSectionContainer {
    padding: 3%;
}


.gameSectionContainer .btn-games {
    background-color: #4ccbd6;
    position: relative;
}



.games-cards {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    height: 10%;
    padding-bottom: 3%;
    margin-top: 4vw;
}


.games-title h2{
    font-weight: 400;
}

.game-card {
    -webkit-box-shadow: 0px 0px 17px -2px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 0px 17px -2px rgba(0,0,0,0.75);
    box-shadow: 0px 0px 17px -2px rgba(0,0,0,0.75);
}



